import React from 'react';
import { graphql } from 'gatsby';
import { Button, TextField, Card, CardContent, Grid, FormControl, Select, FilledInput, Chip, InputLabel } from '@material-ui/core/';
import ArrowForwardIcon from '@material-ui/icons/ArrowForwardSharp';
import ArrowBackIcon from '@material-ui/icons/ArrowBackSharp';
import FileCopySharpIcon from '@material-ui/icons/FileCopySharp';
import { Link } from '@reach/router';

import { Layout, Wrapper, Arrow, CardHeader } from '../components';
import Drama from '../models/Drama';
import dramaDefault from '../utils/drama';
import generatePinkx from '../utils/code';
import Theme from '../models/theme';
import Helmet from 'react-helmet';
import styled from 'styled-components';

const themeColor = require('../../config/Theme');

const HTMLTitle = '挑选主题';

interface Props {
  data: {
    allThemeYaml: any;
  };
}

interface States {
  drama: Drama;
  theme: Theme;
  allThemeYaml: any;
  perview: string;
  title: string;
}

const ThemeSection = styled.section`
  margin-top: 24px;
`;

const List = styled.ul`
  font-size: 16px;
  list-style: none;
  margin: 0;
  padding: 0 16px;
  li {
    margin: 5px;
  }
`;

const State = styled.section`
  margin-top: 0px;
  margin-bottom: 16px;
  padding: 20px;
  background-color: ${themeColor.palette.primary.contrastText};
  color: ${themeColor.palette.secondary.light};
  border-radius: 4px;
  p,
  a {
    font-size: 14px;
    margin: 0;
    color: inherit;
  }
`;

export default class Preview extends React.Component<Props, States> {
  constructor(props: any) {
    super(props);
    let drama: Drama = dramaDefault;
    let theme: Theme = this.props.data.allThemeYaml.edges[0].node;
    const allTheme: any = this.props.data.allThemeYaml.edges;
    if (typeof window !== `undefined`) {
      const dramaJson: any = window.sessionStorage.getItem('drama');
      const themeJson: any = window.sessionStorage.getItem('theme');
      if (dramaJson) {
        drama = JSON.parse(dramaJson);
      }
      if (themeJson) {
        theme = JSON.parse(themeJson);
      }
    }
    this.state = {
      drama,
      theme,
      allThemeYaml: allTheme,
      perview: this.generatePreview(drama, theme),
      title: HTMLTitle,
    };
    this.handleTextFiledChange = this.handleTextFiledChange.bind(this);
    this.handleSelectChange = this.handleSelectChange.bind(this);
    this.handleButtonClick = this.handleButtonClick.bind(this);
  }

  handleSelectChange(event: any) {
    const target = event.target;
    const theme: string = target.options[target.selectedIndex].dataset.theme;
    const drama: Drama = this.state.drama;
    this.setState({ theme: JSON.parse(theme), perview: this.generatePreview(drama, JSON.parse(theme)) });
    if (typeof window !== `undefined`) {
      window.sessionStorage.setItem('theme', theme);
    }
  }

  handleTextFiledChange(event: any) {
    const target = event.target;
    const value: any = target.value;
    this.setState({ perview: value });
  }

  handleButtonClick() {
    const e: any = document.getElementById('code');
    if (e) {
      e.select();
      document.execCommand('Copy');
    }
  }

  generatePreview(drama: Drama, theme: Theme) {
    return generatePinkx(drama, theme)
      .replace(/(\s\s)*/g, '')
      .replace(/\n*/g, '');
  }

  public render() {
    const { perview, title, theme, allThemeYaml } = this.state;
    return (
      <Layout>
        <Helmet title={title} />
        <Wrapper>
          <CardHeader>
            <h2>{title}</h2>
          </CardHeader>
          <CardContent>
            <FormControl fullWidth variant="filled">
              <InputLabel htmlFor="select-theme">选择主题</InputLabel>
              <Select
                autoWidth
                native
                value={theme.id}
                onChange={this.handleSelectChange}
                input={<FilledInput name="themeId" id="select-theme" />}
              >
                {allThemeYaml.map((t: any) => {
                  return (
                    <option value={t.node.id} key={t.node.id} data-theme={JSON.stringify(t.node)}>
                      {t.node.name} by {t.node.author.name}
                    </option>
                  );
                })}
              </Select>
            </FormControl>
            <ThemeSection>
              <Card>
                <CardContent>
                  {!!theme.description && <Chip label={theme.description.normal ? '通用' : '个性'} key={'normal'} className={`themeTag`} />}
                  {!!theme.tags &&
                    theme.tags.map(tag => {
                      return <Chip label={tag} key={tag} className={`themeTag`} />;
                    })}
                </CardContent>
                {!!theme.description && (
                  <List>
                    <Grid container>
                      <Grid item xs={6}>
                        {!!theme.description.coverSize && <li>海报推荐尺寸：{theme.description.coverSize}</li>}
                        {!!theme.description.staff && <li>制作组人数范围：{theme.description.staff}</li>}
                        {!!theme.description.cast && <li>配音组人数范围：{theme.description.cast}</li>}
                        {!!theme.description.ed && <li>片尾组人数范围：{theme.description.ed}</li>}
                        {!!theme.description.links && <li>链接数量范围：{theme.description.links}</li>}
                      </Grid>
                      <Grid item xs={6}>
                        <li>
                          <strong>{theme.description.hover ? '有' : '无'}</strong> 鼠标悬停动画
                        </li>
                        <li>
                          <strong>{theme.description.opening ? '有' : '无'}</strong> 开场动画
                        </li>
                        <li>
                          <strong>{theme.description.animation ? '有' : '无'}</strong> 背景动画
                        </li>
                        <li>
                          <strong>{theme.description.phone ? '有' : '无'}</strong> 手机适配
                        </li>
                      </Grid>
                    </Grid>
                    {!!theme.description.other && <li>补充描述：{theme.description.other}</li>}
                  </List>
                )}
                <img src={theme.cover} className={`themeCover`} />
              </Card>
            </ThemeSection>
          </CardContent>
          <CardContent>
            <State>
              <p>❕版权申明</p>
              <p>使用本软件不得删除PINKX。</p>
              <p>使用主题，不得删除主题作者。</p>
              <p>未经主题作者允许，不得将主题的CSS样式进行二改。</p>
              <p>
                官博：
                <a href="https://weibo.com/u/7017518480" target="_blank">
                  @PINKX编帖助手
                </a>{' '}
                ，主题设计：
                <a href={`https://weibo.com/n/${theme.author.weibo}`} target="_blank">
                  @{theme.author.weibo}
                </a>
              </p>
            </State>
            <TextField
              id="code"
              fullWidth={true}
              variant="filled"
              label="你的代码"
              name="edStr"
              value={perview}
              multiline
              rows={4}
              rowsMax={9}
              onChange={this.handleTextFiledChange}
            />
          </CardContent>
          <Arrow>
            <a href={`/theme-${theme.id}`} target="_blank">
              <Button className={`button`} variant="contained" color="primary">
                预览 <ArrowForwardIcon />
              </Button>
            </a>
            <Button size="large" className={`button`} onClick={this.handleButtonClick} variant="contained" color="primary">
              复制代码 <FileCopySharpIcon />
            </Button>
            <Link to="/generate/">
              <Button size="large" className={`button`} variant="contained" color="secondary">
                <ArrowBackIcon /> 上一步
              </Button>
            </Link>
          </Arrow>
        </Wrapper>
      </Layout>
    );
  }
}

export const GetThemeDefault = graphql`
  query {
    allThemeYaml(sort: { order: ASC, fields: [created] }, filter: { publish: { eq: true } }) {
      edges {
        node {
          author {
            name
            weibo
          }
          id
          name
          tags
          cover
          css
          description {
            normal
            staff
            cast
            ed
            links
            hover
            animation
            opening
            phone
            coverSize
            other
          }
        }
      }
    }
  }
`;
